// Redux & Store
import { useDispatch } from "react-redux";

// Shared
import { queryCreated } from "shared/model";
import { setParams } from "shared/lib";

// Model
import { searchBy, filterBy } from "../../model";

// Lib
import { useParams } from "../../lib";

// Component
import { PaintingsInputs } from "../inputs/paintings-inputs";

// Styles
import styles from "./paintings-filter.module.scss";

export const PaintingsFilter = () => {
  const dispatch = useDispatch();
  const [params, containerRef, paramsHandler] = useParams();

  const handleSearch = () => {
    dispatch(queryCreated({ case: "search", params }));

    setParams({ skip: 0, ...params });
  };

  return (
    <section ref={containerRef} className={styles.root}>
      <div>
        <h2 className={styles.title}>Search by painting name</h2>
        <div>
          {searchBy.map((inputsData) => (
            <PaintingsInputs
              key={inputsData.identifier}
              inputsData={inputsData}
              onChange={paramsHandler}
              onSearch={handleSearch}
            />
          ))}
        </div>
      </div>
      <div>
        <h3 className={styles.title}>Filter by</h3>
        <div className={styles.row}>
          {filterBy.map((inputsData) => (
            <PaintingsInputs
              key={inputsData.identifier}
              inputsData={inputsData}
              onChange={paramsHandler}
              onSearch={handleSearch}
            />
          ))}
        </div>
      </div>
    </section>
  );
};
