import { useRef } from "react";

// Redux & Store
import { useSelector, useDispatch } from "react-redux";

// Features
import { PageNavigation } from "features/page-navigation";

// Shared
import { typicodeApi } from "shared/api";
import { queryCreated } from "shared/model";
import { getParams, setParams } from "shared/lib";

// Component
import { PaintingsItems } from "../items/paintings-items";

// Styles
import styles from "./paintings-list.module.scss";

export const PaintingsList = () => {
  const paintingsPerPage = 20;
  const dispatch = useDispatch();
  const PaintingsTopRef = useRef(null);
  const query = useSelector((state) => state.query.query);
  const {
    data = {},
    isFetching,
    isError,
  } = typicodeApi.useGetPaintingsQuery(query);

  const { data: paintings = [], total: paintingsTotal = 0 } = data;

  const handleNavigate = (selected) => {
    const skip = selected * paintingsPerPage;
    dispatch(queryCreated({ case: "skip", skip }));

    const params = getParams();
    setParams({ ...params, skip });

    PaintingsTopRef.current.scrollIntoView();
  };

  return (
    <section ref={PaintingsTopRef} className={styles.root}>
      <PaintingsItems
        paintings={paintings}
        isFetching={isFetching}
        isError={isError}
      />

      <PageNavigation
        paintingsTotal={paintingsTotal}
        isFetching={isFetching}
        isError={isError}
        paintingsPerPage={paintingsPerPage}
        querySkip={query.skip}
        onPageChange={({ selected }) => handleNavigate(selected)}
      />
    </section>
  );
};
