import PropTypes from "prop-types";
import { useEffect } from "react";

// Features
import { Search } from "features/search/ui/search";

// Shared
import { getParams } from "shared/lib";

// Lib
import { useInput } from "../../lib";

// Styles
import styles from "./paintings-inputs.module.scss";

// Svg
import { ReactComponent as IconReset } from "./assets/icon-close.svg";

export const PaintingsInputs = ({
  inputsData = {},
  onChange = () => {},
  onSearch = () => {},
}) => {
  const [value, setValue, handleChange, handleBlur, handelReset] = useInput("");

  const { identifier, placeholder } = inputsData;

  useEffect(() => {
    const params = getParams();

    if (params[identifier]) {
      setValue((value) => params[identifier]);
    }
    // eslint-disable-next-line
  }, [identifier]);

  useEffect(() => {
    onChange(value, identifier);
    // eslint-disable-next-line
  }, [value]);

  const handleKeyUp = (e) => {
    if (e.key === "Enter") {
      onSearch();
    }
  };

  return (
    <div className={styles.root}>
      <div data-testid="paintings-input" className={styles.input_wrapper}>
        <input
          className="xs-text collapse-text"
          type="text"
          placeholder={placeholder}
          value={value}
          data-id={identifier}
          onChange={handleChange}
          onBlur={handleBlur}
          onKeyUp={handleKeyUp}
        />
        {value && <IconReset className={styles.reset} onClick={handelReset} />}
      </div>
      <Search className={styles.search} onClick={onSearch} />
    </div>
  );
};

PaintingsInputs.propTypes = {
  data: PropTypes.object,
  onChange: PropTypes.func,
  onClick: PropTypes.func,
};
