import PropTypes from "prop-types";
import cn from "classnames";
import ReactPaginate from "react-paginate";

// Lib
import { useForcePage, usePageCount } from "../lib";

// Svg
import { ReactComponent as IconArrow } from "./assets/icon_arrow.svg";

// Styles
import styles from "./page-navigation.module.scss";

export const PageNavigation = ({
  paintingsTotal = 0,
  isFetching = false,
  isError = false,
  paintingsPerPage = 0,
  querySkip = 0,
  onPageChange = () => {},
}) => {
  const [pageCount] = usePageCount(paintingsTotal, paintingsPerPage);
  const [forcePage] = useForcePage(querySkip, paintingsPerPage);

  if (isError || isFetching || pageCount === 0) return null;

  return (
    <ReactPaginate
      onPageChange={onPageChange}
      pageRangeDisplayed={3}
      marginPagesDisplayed={1}
      forcePage={forcePage}
      pageCount={pageCount}
      renderOnZeroPageCount={null}
      // Previous
      previousLabel={
        <IconArrow
          data-testid="page-navigation-svg"
          className={cn(styles.svg, styles.svg_prev)}
        />
      }
      // Next
      nextLabel={
        <IconArrow data-testid="page-navigation-svg" className={styles.svg} />
      }
      // Break
      breakLabel="..."
      // Page
      containerClassName={styles.root}
      // Other
      activeClassName={styles.active}
      disabledClassName={styles.disabled}
    />
  );
};

PageNavigation.propTypes = {
  paintingsTotal: PropTypes.number,
  isFetching: PropTypes.bool,
  isError: PropTypes.bool,
  itemsPerPage: PropTypes.number,
  querySkip: PropTypes.number,
  onPageChange: PropTypes.func,
};
